import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import AuthForm from '../../components/auth/AuthForm';
import { useDispatch, useSelector } from 'react-redux';
import { changeFieid, register, initializeForm } from '../../module/redux/auth';

const RegisterForm = ({ history }) => {
  const [error, setError] = useState(null);
  const dispatch = useDispatch();
  const { form, auth, authError } = useSelector(({ auth }) => ({
    form: auth.register,
    auth: auth.auth,
    authError: auth.authError,
  }));

  const onChange = e => {
    const { value, name } = e.target;
    dispatch(changeFieid({ form: 'register', key: name, value }));
    setError(null);
  };

  const onSubmit = e => {
    e.preventDefault();
    const { username, password, passwordConfirm } = form;
    //   
    if ([username, password, passwordConfirm].includes('')) {
      setError('   .');
      return;
    }
    //   
    if (password !== passwordConfirm) {
      setError('  .');
      dispatch(changeFieid({ form: 'register', key: 'password', value: '' }));
      dispatch(
        changeFieid({ form: 'register', key: 'passwordConfirm', value: '' }),
      );
      return;
    }
    dispatch(register({ username, password }));
  };

  //       form 
  useEffect(() => {
    dispatch(initializeForm('register'));
  }, [dispatch]);

  //  / 
  useEffect(() => {
    if (authError) {
      //    
      if (authError.response.status === 409) {
        setError('  .');
        return;
      }
      //  
      setError(' ');
      console.log(authError);
      return;
    }
    if (auth) {
      console.log(auth);
      history.push('/login');
    }
  }, [auth, authError, dispatch, history]);

  return (
    <AuthForm
      type="register"
      form={form}
      onChange={onChange}
      onSubmit={onSubmit}
      error={error}
    />
  );
};

export default withRouter(RegisterForm);
