import React from 'react';
import AskModal from '../common/AskModal';

const AskRemoveModal = ({ className, visible, onConfirm, onCancel }) => {
  return (
    <AskModal
      className={className}
      visible={visible}
      title="DELETE POST"
      description=" POST ?"
      confirmText=""
      onConfirm={onConfirm}
      onCancel={onCancel}
    />
  );
};

export default AskRemoveModal;
